import React from 'react'; import styled from 'styled-components'; 

const Content = (props) => (
    <ContentContainer>
        {props.children}
    </ContentContainer>
)

export const ContentContainer = styled.div `
    width: 98%; min-height: 93vh; height: 83%; margin: 4px auto; overflow: auto; 
    border-radius: 60px 125px 60px 60px; font-family: 'Architects Daughter', cursive;
    box-shadow: -10px 10px 20px 0px #FEC; z-index: 1; 
    `




export default Content